.blockProjects {
  width: 100%;
  height: auto;
  background-color: #111;
  padding: 90px 0 0 0;
  box-sizing: border-box;

  .containerProjects {
    width: 80%;
    height: auto;
    margin: 0 auto;
    //display: flex;
    //flex-wrap: wrap;
    //gap: 20px;
    box-sizing: border-box;

    .blockTitle {
      width: 100%;
      height: 100px;
      border-bottom: 1px solid;
      padding-bottom: 50px;

      display: flex;
      flex-direction: column;
      justify-content: center;

      @media (max-width: 360px) {
        width: 100%;
        height: 80px;
        border-bottom: 1px solid;
        display: flex;
        justify-content: center;
      }
    }
  }

  .boxBorders_2 {
    position: relative;
    padding-top: 90px;

    &::before {
      position: absolute;
      content: '';
      top: 100%;
      left: 50%;
      width: 6px;
      height: 70px;
      background-color: #4973ff;
      background: rgb(2, 0, 36);
      background: linear-gradient(180deg, #4973ff 0%, #4973ff 25%, rgba(0, 212, 255, 1) 100%);
      transform: translate(-50%, -50%);
      z-index: 2;
    }

    &::after {
      content: '';
      position: absolute;
      top: 100%;
      right: 0;
      width: 50%;
      border: 1px dashed #333;
      transform: scaleY(0.8);
      transform-origin: top;
      z-index: 1;
    }
  }

  @media (max-width: 360px) {
    width: 100%;
    //min-height: 75vh;
  }
}


